@keyframes blink {
	0% {
		transform: scale(1);
		opacity: 1; }
	15% {
		transform: scale(1.1);
		opacity: 1; }
	30% {
		transform: scale(0.9);
		opacity: 0.8; }
	45% {
		transform: scale(1.15);
		opacity: 1; }
	60% {
		transform: scale(0.95);
		opacity: 0.8; }
	75% {
		transform: scale(1.1);
		opacity: 1; }
	90% {
		transform: scale(0.9);
		opacity: 0.8; }
	100% {
		transform: scale(1);
		opacity: 1; } }
